.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 20px auto;
  max-width: 80%; 
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  width: 100%;
  max-width: 400px; 
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid rgba(128, 128, 128, 0.37);
  border-radius: 5px;
  font-size: 16px; 
}

.buttons {
  display: flex;
  gap: 10px; 
}

.btn {
  padding: 10px 15px;
  font-size: 16px; 
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.btn-submit {
  background-color: green;
  color: white;
}

.btn-reset {
  background-color: rgb(209, 29, 29);
  color: white;
}

.btn-reset:hover {
  background-color: #494e52;
}

.table {
  width: 100%;
  max-width: 800px; 
  border-collapse: collapse;
  margin-top: 20px;
}

.table th, .table td {
  padding: 10px;
  border: 1px solid rgba(128, 128, 128, 0.37);
  text-align: left;
}

.table th {
  background-color: rgba(128, 128, 128, 0.37);
  font-weight: bold;
}

@media (max-width: 900px) {
  .container {
    max-width: 95%; 
  }

  .table {
    font-size: 14px; 
  }
}

@media (max-width: 500px) {
  h1 {
    font-size: 24px; 
  }

  .btn {
    font-size: 14px; 
  }
}
